export default {
	name: "staff",
	props: ["staffList"],
	data() {
		return {
			urls: []
		}
	},
	methods:{
		showBigPic(index) {
			uni.previewImage({
				urls: this.urls,
				currrent: this.urls[index]					
			})
		}
	},
	created() {
		for (let x=0;x<this.staffList.length;x++) {
			this.urls.push(this.staffList[x].photo)
		}
	},
}
